import { Record } from "immutable";

import { SELECT_REPO, START, SUCCESS, FAIL, DEFAULT } from "../constants";

const ReducerState = Record({
  loading: false,
  error: false,
  issues: [],
  selectrepo: "",
  startSession: true
});

const defaultState = new ReducerState();

export default (state = defaultState, action) => {
  const { type, response, payload } = action;

  switch (type) {
    case SELECT_REPO + START:
      return state.set("loading", true).set("startSession", false);
    case SELECT_REPO + SUCCESS:
      return state
        .set("loading", false)
        .set("error", false)
        .set("issues", response)
        .set("selectrepo", payload);
    case SELECT_REPO + FAIL:
      return state.set("loading", false).set("error", true);
    case SELECT_REPO + DEFAULT:
      return state
        .set("loading", false)
        .set("error", false)
        .set("issues", [])
        .set("startSession", true)
        .set("selectrepo", "");
    default:
      return state;
  }
};



