import { START, SUCCESS, FAIL } from "../constants";

export default store => next => action => {
  const { callAPI, type, ...rest } = action;
  if (!callAPI) return next(action);
  next({
    ...rest,
    type: type + START
  });

  setTimeout(() => {

    fetch(callAPI)
    .then(res => res.json())
    .then(response => {
      if (response.message) {
        return next(console.log(response) || { ...rest, type: type + FAIL });
      }
      return next({ ...rest, type: type + SUCCESS, response });
    })
    .catch(error => next(console.log(error) || { ...rest, type: type + FAIL }));
  }, 500)
};



