import React, { Component } from "react";
import styled from "styled-components";

import InputName from "./InputName";
import SelectRepo from "./SelectRepo";
import OwnerRepoInfoBlock from "./OwnerRepoInfoBlock";
import IssuesInfoBlock from "./IssuesInfoBlock";

class HomePage extends Component {
  render() {
    return (
      <Section>
        <Container>
          <ContainerOwner>
            <OwnerRepoInfoBlock />
          </ContainerOwner>
          <Wrap>
            <InputName />
            <SelectRepo />
          </Wrap>
        </Container>
        <div>
          <IssuesInfoBlock />
        </div>
      </Section>
    );
  }
}

export default HomePage;

const Section = styled.div`
  display: flex;
  flex-direction: column;
`;

const ContainerOwner = styled.div`
  width: 250px;
  min-height: 250px;
  display: inline-flex;
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  max-width: 288px;
`;

const Container = styled.div`
  display: flex;
`;



