import {
  URL_SERVER,
  SEARCH_OWNER,
  SEARCH_REPOS_USER,
  SELECT_REPO,
  DEFAULT
} from "../constants";

export const searchUser = userName => {
  return dispatch => {
    dispatch({
      type: SELECT_REPO + DEFAULT
    });

    dispatch({
      type: SEARCH_OWNER,
      callAPI: `${URL_SERVER}users/${userName}`
    });

    dispatch({
      type: SEARCH_REPOS_USER,
      callAPI: `${URL_SERVER}users/${userName}/repos?per_page=100`
    });
  };
};

export const selectRepo = (path, name) => {
  return {
    type: SELECT_REPO,
    callAPI: `${URL_SERVER}repos/${path}/issues?per_page=100`,
    payload: name
  };
};



