import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Link } from "react-router-dom";

import Preloader from "../custom/preloader/Preloader";
import ErrorLoadData from "../custom/errorLoadData/ErrorLoadData";

import Pagination from "./issuesInfoBlock/Pagination";

class IssuesInfoBlock extends Component {
  state = {
    start: 0,
    end: 10,
    steps: 10
  };

  paginationIssues = (start, end) => {
    this.setState({ start: start, end: end });
  };

  changeSteps = event => {
    const { value } = event.target;
    this.setState({
      steps: value,
      start: 0,
      end: value
    });
  };

  render() {
    const {
      issues,
      loading,
      error,
      startSession,
      selectrepo,
      loginRepoOwner
    } = this.props;
    const { start, end, steps } = this.state;

    const amountIssues = issues.length;

    if (startSession && !!amountIssues) {
      return <p>Выберите репозитории</p>;
    }

    if (loading) return <Preloader text={"вопросов"} />;
    if (error) return <ErrorLoadData text={"данных"} />;

    if (!startSession && !loading && !amountIssues) {
      return <p>В данном репозитории нету вопросов.</p>;
    }

    return (
      <Wrap>
        {amountIssues > 1 && (
          <Pagination
            count={amountIssues}
            steps={steps}
            paginationIssues={this.paginationIssues}
            changeSteps={this.changeSteps}
          />
        )}
        {issues.slice(start, end).map(issue => {
          const { id, title, number, created_at, user } = issue;
          const { login, avatar_url, html_url } = user;
          return (
            <Container key={id}>
              <Avatar href={html_url}>
                <AvatarImg src={avatar_url} alt={`avatar of ${login}`} />
              </Avatar>
              <WrapText>
                <Link to={`${loginRepoOwner}/${selectrepo}/issues/${number}`}>
                  {title}
                </Link>
                <Span>
                  # {number} opened: {created_at.match(/\d{4}-\d{2}-\d{2}/)} by
                  <LinkLogin href={html_url}>{login}</LinkLogin>
                </Span>
              </WrapText>
            </Container>
          );
        })}
      </Wrap>
    );
  }
}

export default connect(state => {
  return {
    loading: state.issuesInRepo.loading,
    error: state.issuesInRepo.error,
    issues: state.issuesInRepo.issues,
    selectrepo: state.issuesInRepo.selectrepo,
    startSession: state.issuesInRepo.startSession,
    loginRepoOwner: state.searchOwner.ownerRepo.login
  };
})(IssuesInfoBlock);

IssuesInfoBlock.propTypes = {
  loading: PropTypes.bool,
  error: PropTypes.bool,
  issues: PropTypes.array,
  selectrepo: PropTypes.string,
  startSession: PropTypes.bool,
  login: PropTypes.string
};

const Wrap = styled.div``;

const Avatar = styled.a`
  display: inline-flex;
`;

const AvatarImg = styled.img`
  width: 80px;
  height: 80px;
`;

const Container = styled.div`
  display: flex;
  margin: 20px auto 20px 20px;
`;

const WrapText = styled.div`
  margin: 0 0 0 20px;
  display: flex;
  flex-direction: column;

  > a {
    text-decoration: none;
    color: inherit;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
      sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: bold;
    font-size: 25px;
    margin: 0 0 10px;
  }
`;

const Span = styled.span`
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 15px;
`;

const LinkLogin = styled.a`
  text-decoration: none;
  margin: 0 0 0 8px;
`;



