import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Pagination = props => {
  const { steps, count } = props;
  const countPages = Array.from(
    { length: Math.ceil(count / steps) },
    (_, index) => index + 1
  );
  const maximumSteps = count > steps ? steps : count;

  return (
    <Container>
      <div>
        <p>
          Вопросов в данном репозитории:
          <Span>{count}</Span>
        </p>
        <p>
          Количество отображаемых вопросов на странице:{" "}
          <Span>{maximumSteps}</Span>
        </p>
        <Wrap>
          <Span>2</Span>
          <input
            type="range"
            min="2"
            max={count}
            value={maximumSteps}
            onChange={e => props.changeSteps(e)}
          />
          <Span>{count}</Span>
        </Wrap>
      </div>
      {countPages.map((page, index) => {
        const numberInPage = page * steps;
        return (
          <button
            key={index}
            onClick={() => props.paginationIssues(index * steps, numberInPage)}
          >
            {numberInPage - steps + 1} -{" "}
            {numberInPage < count ? numberInPage : count}
          </button>
        );
      })}
    </Container>
  );
};

export default Pagination;

Pagination.propTypes = {
  steps: PropTypes.any,
  count: PropTypes.number,
  paginationIssues: PropTypes.func,
  changeSteps: PropTypes.func
};

const Container = styled.div`
  margin: 20px;
`;

const Span = styled.span`
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: bold;
  font-size: 25px;
  margin: 0 5px;
`;

const Wrap = styled.div`
  display: flex;
`;



