import React, { Component } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

import { URL_SERVER } from "../../constants";

import Preloader from "../custom/preloader/Preloader";
import ErrorLoadData from "../custom/errorLoadData/ErrorLoadData";

class DetailedReviewIssue extends Component {
  state = {
    loaded: false,
    error: false,
    data: {}
  };

  componentDidMount() {
    const { url } = this.props.match;
    const path = `${URL_SERVER}repos${url}`;
    fetch(path)
      .then(res => res.json())
      .then(res => this.setState({ loaded: true, data: res }))
      .catch(
        error =>
          console.log(error) || this.setState({ loaded: true, error: true })
      );
  }

  render() {
    const { loaded, data } = this.state;

    if (!loaded) return <Preloader text={"nety"} />;
    if (loaded && data.message) return <ErrorLoadData text={"данных"} />;

    const { title, body, number, user: { html_url, avatar_url, login } } = data;

    return (
      <section>
        <Link to="/">На главную</Link>
        <div>
          <H1>{title}</H1>
          <P>#{number}</P>
          <Wrap>
            <LinkImg href={html_url}>
              <AvatarImg src={avatar_url} alt="avatar" />
            </LinkImg>
            <div>
              <LinkName href={html_url}>{login}</LinkName>
              <p>{body}</p>
            </div>
          </Wrap>
        </div>
      </section>
    );
  }
}

export default DetailedReviewIssue;

const H1 = styled.h1`
  display: inline-block;
  margin-right: 20px;
  font-size: 32px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
`;

const P = styled.p`
  display: inline-block;
  font-size: 32px;
  font-weight: 300;
  color: #a3aab1;
  letter-spacing: -1px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
`;

const AvatarImg = styled.img`
  border-radius: 5%;
  max-width: 100%;
`;

const LinkName = styled.a`
  text-decoration: none;
  font-size: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #000000;
  line-height: 30px;
  display: inline-flex;
  transition: all 0.5s ease-out 0.1s;

  :hover {
    text-decoration: underline;
    color: rgba(0, 159, 255, 0.7);
  }
`;

const LinkImg = styled.a`
  min-width: 100px;
  max-width: 120px;
  margin-right: 10px;
`;

const Wrap = styled.div`
  display: flex;
`;



