import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import styled from "styled-components";

import { selectRepo } from "../../AC/index";

import Preloader from "../custom/preloader/Preloader";

class SelectRepo extends Component {
  PropTypes = {
    loading: PropTypes.bool,
    selectRepo: PropTypes.func.isRequired,
    repositories: PropTypes.array.isRequired,
    loginOwnerRepo: PropTypes.object.isRequired
  };

  state = {
    SelectedRepo: "---"
  };

  handleChange = event => {
    const { value } = event.target;
    const { selectRepo, loginOwnerRepo } = this.props;
    const path = `${loginOwnerRepo}/${value}`;

    this.setState({ SelectedRepo: value });
    selectRepo(path, value);
  };

  render() {
    const { repositories, loginOwnerRepo, loading } = this.props;
    const amountRepos = repositories.length;

    if (!loginOwnerRepo) return null;
    if (loading) return <Preloader text={"репозиториев"} />;
    if (!amountRepos) {
      return <Title>У данного пользователя нету репозиториев.</Title>;
    }

    return (
      <Container>
        <Wrap>
          <Label>
            Выберите репозиторий:
            <Select
              value={this.state.SelectedRepo}
              onChange={this.handleChange}
            >
              <Option disabled> --- </Option>
              {repositories.map((repo, index) => {
                return <Option key={index}>{repo.name}</Option>;
              })}
            </Select>
          </Label>
        </Wrap>
      </Container>
    );
  }
}

export default connect(
  state => {
    return {
      loading: state.searchReposUser.loading,
      repositories: state.searchReposUser.repositories,
      loginOwnerRepo: state.searchOwner.ownerRepo.login
    };
  },
  { selectRepo }
)(SelectRepo);

const Container = styled.div`
  margin-top: 30px;
`;

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const Title = styled.p`
  margin: 0;
  font-size: 25px;
`;

const Label = styled.label`
  font-size: 25px;
  display: flex;
  flex-direction: column;
`;

const Select = styled.select`
  font-size: 16px;
  margin-top: 25px;
`;

const Option = styled.option`
  font-size: 16px;
`;



