import { Record } from "immutable";

import { SEARCH_REPOS_USER, START, SUCCESS, FAIL } from "../constants";

const ReducerState = Record({
  loading: false,
  error: false,
  repositories: []
});

const defaultState = new ReducerState();

export default (state = defaultState, action) => {
  const { type, response } = action;
  switch (type) {
    case SEARCH_REPOS_USER + START:
      return state.set("loading", true).set("error", false);
    case SEARCH_REPOS_USER + SUCCESS:
      return state
        .set("loading", false)
        .set("error", false)
        .set("repositories", response);
    case SEARCH_REPOS_USER + FAIL:
      return state
        .set("loading", false)
        .set("error", true)
        .set("repositories", []);
    default:
      return state;
  }
};



