import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import styled from "styled-components";

import { searchUser } from "../../AC/index";

class InputName extends Component {
  PropTypes = {
    searchUser: PropTypes.func.isRequired
  };

  state = {
    userName: "defunkt"
  };

  changeUserName = event => {
    this.setState({ userName: event.target.value });
  };

  callAPI = () => {
    this.props.searchUser(this.state.userName);
  };

  render() {
    return (
      <Wrap>
        <Title>Введите имя пользователя:</Title>
        <Container>
          <Input
            value={this.state.userName}
            onChange={this.changeUserName}
            placeholder="Имя пользователя"
          />
          <Button onClick={this.callAPI}>Search user</Button>
        </Container>
      </Wrap>
    );
  }
}

export default connect(null, { searchUser })(InputName);

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const Title = styled.p`
  font-size: 25px;
`;

const Input = styled.input`
  display: inline-flex;
`;

const Button = styled.button`
  cursor: pointer;
`;

const Container = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
`;



