import { Record } from "immutable";

import { SEARCH_OWNER, START, SUCCESS, FAIL } from "../constants";

const ReducerState = Record({
  loading: false,
  error: false,
  ownerRepo: {}
});

const defaultState = new ReducerState();

export default (state = defaultState, action) => {
  const { type, response } = action;
  switch (type) {
    case SEARCH_OWNER + START:
      return state.set("loading", true).set("error", false);
    case SEARCH_OWNER + SUCCESS:
      return state
        .set("loading", false)
        .set("error", false)
        .set("ownerRepo", response);
    case SEARCH_OWNER + FAIL:
      return state
        .set("loading", false)
        .set("error", true)
        .set("ownerRepo", {});
    default:
      return state;
  }
};



