import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import styled from "styled-components";

import Preloader from "../custom/preloader/Preloader";
import ErrorLoadData from "../custom/errorLoadData/ErrorLoadData";

const OwnerRepoInfoBlock = props => {
  const { ownerRepo, loading, error } = props;
  const { login, html_url, avatar_url } = ownerRepo;

  if (loading) return <Preloader text={"пользователя"} />;
  if (error) return <ErrorLoadData text={"данного пользователя"} />;
  if (!login) return null;

  return (
    <div>
      <LinkImg href={html_url}>
        <AvatarImg src={avatar_url} alt="avatar" />
      </LinkImg>
      <LinkName href={html_url}>{login}</LinkName>
    </div>
  );
};

export default connect(state => {
  return {
    loading: state.searchOwner.loading,
    error: state.searchOwner.error,
    ownerRepo: state.searchOwner.ownerRepo
  };
})(OwnerRepoInfoBlock);

OwnerRepoInfoBlock.propTypes = {
  loading: PropTypes.bool,
  error: PropTypes.bool,
  ownerRepo: PropTypes.object
};

const AvatarImg = styled.img`
  display: inline-flex;
  border-radius: 5%;
  max-width: 100%;
`;

const LinkName = styled.a`
  text-decoration: none;
  font-size: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #000000;
  line-height: 30px;
  margin-top: 25px;
  display: inline-flex;
  transition: all 0.5s ease-out 0.1s;

  :hover {
    color: rgba(0, 159, 255, 0.7);
  }
`;

const LinkImg = styled.a`
  display: flex;
  justify-content: center;
  align-items: center;
`;



